<template>
  <div class="user" v-if="user">
    <div class="user__left">
      <img :src="user.avatar_url" alt="" />
      <a :href="user.html_url" class="user__btn">ПОСЕТИТЬ</a>
    </div>
    <div class="user__right">
      <p>{{ user.login }}</p>
      <p>
        Репозиториев: <span>{{ user.public_repos }}</span>
      </p>
      <p>
        Создан:
        <span>{{ new Date(user.created_at).toLocaleDateString() }}</span>
      </p>
      <p>
        Подписщиков: <span>{{ user.followers }}</span>
      </p>
      <p>
        Подписок: <span>{{ user.following }}</span>
      </p>
    </div>
  </div>

  <div class="sort">
    <h2 class="sort__title">Сортировка</h2>
    <div class="sort__box">
      <button
        @click="sortRepos(i, btn.sortType)"
        :class="{ active: isActive == i }"
        v-for="(btn, i) in btns"
        :key="i"
        class="sort__box-btn"
      >
        {{ btn.name }}
      </button>
    </div>
  </div>
</template>

<script>
import { mapState, mapMutations } from "vuex";
export default {
  computed: {
    ...mapState(["user"]),
  },

  data() {
    return {
      btns: [
        { name: "Имя", sortType: "name" },
        { name: "Звезди", sortType: "stargazers_count" },
        { name: "Дата", sortType: "created_at" },
      ],
      isActive: 0,
    };
  },
  methods: {
    ...mapMutations(["sort"]),
    sortRepos(i, sortType) {
      this.isActive = i;
      this.sort(sortType)
      
    },
  },
};
</script>

<style lang="scss">
.sort {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 20px 0;
  gap: 10px;
  &__title {
    color: #3f3f3f;
    font-size: 18px;
    font-weight: 700;
    line-height: 120%;
    text-transform: capitalize;
  }
  &__box {
    background: #e0e0e0;
    border-radius: 4px;
    overflow: hidden;
    padding: 2px;
    &-btn {
      border: none;
      outline: none;
      background: transparent;
      color: #3f3f3f;
      font-size: 16px;
      font-weight: 700;
      line-height: 120%;
      text-transform: capitalize;
      padding: 6px 36px;
      border-radius: 6px;
      cursor: pointer;
      &.active {
        background: white;
      }
    }
  }
}
</style>