<template>
  <div class="search">
    <div class="search__box">
      <input 
        type="text" 
        class="search__input"
        placeholder="Введите имя пользователя"
        v-model="search"
        @keydown.enter="getUserInfo"
       />
      <span @click="removeItem">❌</span>
    </div>
    <button @click="getUserInfo" class="search__btn">НАЙТИ</button>
  </div>
</template>

<script>
import { mapState, mapActions } from "vuex";
export default {
  data(){
    return{
      search: ''
    }
  },
  methods:{
    ...mapActions(['userInfo']),
    getUserInfo(){
      this.userInfo(this.search)
    },
    removeItem(){
      this.search = ''
    }
  }
};
</script>

<style></style>
